.supergames dd {
  display: inline-block;
  border-top: 1px solid #ffffff;
  margin: 0;
  margin-bottom: 20px;
  font-weight: 700;
  font-size: 40px;
  line-height: 50px; }
  @media (max-width: 1199px) {
    .supergames dd {
      font-size: 24px;
      line-height: 30px;
      margin-bottom: 7px; } }

.supergames__date::after {
  display: inline-block;
  max-width: 40px;
  width: min-content;
  margin-left: 7px;
  content: " 2019";
  font-weight: 400;
  font-size: 14px;
  line-height: 150%; }
  @media (max-width: 1199px) {
    .supergames__date::after {
      font-size: 10px;
      line-height: 120%;
      margin-left: 3px;
      width: min-content; } }

.supergames p {
  font-size: 18px;
  margin-top: -5px; }
  @media (max-width: 1199px) {
    .supergames p {
      font-size: 12px;
      margin-top: -7px; } }

.supergames a {
  display: block;
  margin-top: auto;
  color: inherit;
  text-transform: uppercase;
  text-align: center;
  padding: 25px;
  border: 4px solid #ffffff;
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em; }
  @media (max-width: 1199px) {
    .supergames a {
      padding: 12px;
      border: 2px solid #ffffff;
      font-size: 10px;
      line-height: 12px; } }
  .supergames a:hover {
    background-color: #ffffff;
    color: #102654; }

.trainers {
  padding-top: 0; }

.trainers__list {
  list-style: none;
  padding: 0;
  margin-top: 51px;
  height: 294px; }

.trainers__item {
  width: 260px; }

.trainer-card {
  position: relative;
  height: 100%; }

.trainer-card h3 {
  position: absolute;
  bottom: 24px;
  margin: 0;
  width: 100%;
  text-align: center;
  color: #ffffff;
  font-weight: 700;
  font-size: 18px; }

.trainer-card:hover h3,
.trainer-card:active h3 {
  z-index: 2;
  top: 52px; }

.trainer-card__pic-wrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  z-index: -1; }

.trainer-card img {
  position: absolute;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover; }

.trainer-card__text-wrapper {
  position: relative;
  height: 100%;
  width: 100%;
  overflow: auto; }

.trainer-card__text {
  top: 0;
  position: absolute;
  width: 100%;
  padding-top: 58px;
  padding-bottom: 10px;
  background-color: rgba(16, 38, 84, 0.89);
  color: #ffffff; }

.trainer-card:hover .trainer-card__text,
.trainer-card:focus-within .trainer-card__text {
  display: block; }

.trainer-card p {
  text-align: center;
  color: #b6bdcc; }

.trainer-card ul {
  list-style: disc;
  padding-left: 68px;
  padding-right: 15px;
  margin-top: 33px;
  font-size: 14px;
  line-height: 150%; }

.trainer-card li {
  padding-left: 3px;
  margin-bottom: 13px; }

